.card {
  width: 21%;
  border-radius: 1rem;
  background: white;
  padding: 2rem;
  box-shadow: 2px 2px 10px 10px rgba(0, 0, 0, 0.75);
  transition: 0.2s;
}

.card:not(:first-child) {
  margin-left: -2rem;
}
.card:hover {
  transform: translateY(-1rem);
}
.card:hover ~ .card {
  transform: translateX(2rem);
}


